@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One&family=Poppins:wght@400&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-weight: 500;
}

html,
body {
  font-family: 'Poppins', sans-serif;
}

::-webkit-scrollbar {
  width: 3px;
}

button {
  padding: 5px 10px;
  border-radius: 5px;
  transition: all 0.2s;
  outline: none;
  border: none;
  cursor: pointer;
  font-weight: bold;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    transform: scale(0.9);
  }
}

.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.darken-Black {
  background-color: #000000;
  color: #ffffff;
}

.coloured-Grey {
  background-color: #636363;
  color: #000000;
}

.coloured-Green {
  background-color: #CCFFCC;
  color: #000000;
}

.coloured-Pink {
  background-color: #f588ff;
  color: #000000;
}

.coloured-Yellow {
  background-color: #cabd00;
  color: #000000;
}

.coloured-LightPurple {
  background-color: #9999FF;
  color: #000000;
}

.coloured-Blue {
  background-color: #33CCFF;
  color: #000000;
}

.lighten-White {
  background-color: #FFFFFF;
  color: #000000;
}

.container {
  width: 80%;
}

header {
  font-family: 'Black Ops One', sans-serif;
  width: 100%;
  display: flex;

  &>h1 {
    font-weight: 400;

    &>span {
      font-size: 26px !important;
    }
  }
}

.timerSection {
  margin: 5px 0;
  width: 100%;
  display: flex;
}

.textContainer {
  width: 95%;
  margin: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
}

.textArea {
  width: 100%;
  background: transparent;
  font-size: 1.5rem;
  outline: none;
  border: none;
  padding: 5px;
  border-radius: 3px;
  resize: none;
}

.paraBox {
  width: 100%;
  padding: 5px;
  border-radius: 3px;
  height: 300px;
}


.paragraghText {
  width: 100%;
  max-height: 100%;
  font-size: 1.3rem;
  overflow-y: scroll;
  display: flex;
  flex-wrap: wrap;
}

.correct {
  color: green;
  background-color: rgb(196, 252, 196);
}

.incorrect {
  color: red;
  background-color: rgb(253, 211, 211);
}

footer {
  display: flex;
  margin: 10px 0;
}

.social-links {
  display: flex;
  font-size: 1.5rem;
  gap: 20px;
  cursor: pointer;
}

.buttonSection {
  display: flex;
  gap: 10px;
}

.theme-Switcher {
  padding: 8px;
  margin-left: auto;
  width: 120px;
  outline: none;
  border: none;
  border-radius: 5px;

  & :focus {
    border: 1px solid rgb(28, 28, 160);
  }
}

.options {
  margin: 5px;
}

.scoreBoard {
  display: flex;
  justify-content: space-between;

  & .rightPart {
    width: 30%;
    min-height: 370px;
    position: relative;
    margin: 17px 0;

    & button {
      position: absolute;
      bottom: 0;
    }
  }

  & .graphSection {
    width: 68%;
    border: 1px solid grey;
  }
}

.typingStatus {
  &>* {
    margin: 3px 0;
  }
}

.input-field {
  opacity: 0;
  z-index: -999;
  position: absolute;
}


.typing-text p span.active {
  color: #17A2B8;
}

.typing-text p span.active::before {
  position: absolute;
  content: "";
  height: 2px;
  width: 100%;
  bottom: 0;
  left: 0;
  opacity: 0;
  border-radius: 5px;
  background: #17A2B8;
  animation: blink 1s ease-in-out infinite;
}

/* ----------------------------------------- */
.align-right {
  margin-left: auto;
}

#cursor {
  display: none;
  width: 2px;
  height: 1.6rem;
  background: var(--primaryColor);
  position: fixed;
  top: 198px;
  left: 18px;
  animation: blink .3s infinite;
}

@keyframes blink {
  50% {
    opacity: 1;
  }
}